._6pp-basic-slider {
  /* removing default appearance */
  -webkit-appearance: none;
  appearance: none;
  /* creating a custom design */
  width: 100%;
  cursor: pointer;
  outline: none;
  border-radius: 15px;
  /*  overflow: hidden;  remove this line*/

  /* New additions */
  height: 6px;
  background: #ccc;
}

/* Thumb: webkit - Orange */
._6pp-basic-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  height: 15px;
  width: 15px;
  background-color: white;
  border-radius: 50%;
  border: none;
  transition: 0.2s ease-in-out;
}

/* Thumb: Firefox */
._6pp-basic-slider::-moz-range-thumb {
  height: 15px;
  width: 15px;
  background-color: white;
  border-radius: 50%;
  border: none;
  /* box-shadow: -407px 0 0 400px #f50; emove this line */
  transition: 0.2s ease-in-out;
}

/* Hover, active & focus Thumb: Webkit */

._6pp-basic-slider::-webkit-slider-thumb:hover {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.1);
}
._6pp-basic-slider:active::-webkit-slider-thumb {
  box-shadow: 0 0 0 13px rgba(255, 255, 255, 0.2);
}
._6pp-basic-slider:focus::-webkit-slider-thumb {
  box-shadow: 0 0 0 13px rgba(255, 255, 255, 0.2);
}

/* Hover, active & focus Thumb: Firfox */

._6pp-basic-slider::-moz-range-thumb:hover {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.1);
}
._6pp-basic-slider:active::-moz-range-thumb {
  box-shadow: 0 0 0 13px rgba(255, 255, 255, 0.2);
}
._6pp-basic-slider:focus::-moz-range-thumb {
  box-shadow: 0 0 0 13px rgba(255, 255, 255, 0.2);
}

